.navbar2 {
    background:  #4267B2;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
  }
  li{
    list-style: none;
  }
  .testme{
    color:red;
  }
  .activel.nav-links2{
    background-color: white;
    color: blue;
    font-size: 40;
  }
  
  .active1{
    background-color: white;
    color: blue;
    font-size: 40;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 3%;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu2 {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item2 {
    display: flex;
    align-items: center;
    height: 80px;
  }
  
  .nav-links2 {
    margin-top: 16px;
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links2:hover {
    background-color: white;
    color: blue;
    transition: all 0.2s ease-out;
  }
  .nav-links2.active1 {
    background-color: white;
    color: blue;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  .mobileshow{
    display: none;
  }
  .desktophide{
    display:none;
  
  }
  @media screen and (max-width: 960px) {
    .navbar2{
      margin-top: 0%;
      margin-bottom: 5% ;
      height: 100px;
      margin-right: 0px;
      height: 55px;
    }
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu2 {
      margin-top: 0%;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 55px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    
    }
  
    .nav-menu2.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      top: 9%;
      height: 100%;
    }
  
    .nav-links2 {
      text-align: center;
      width: 100%;
      margin-left: -5%;
      display: table;
      
    }
  
    .nav-links2:hover {
      background-color: #1888ff;
      border-radius: 0;
    }
  
    
    .menu-icon {
      display: block;
      right: 10px;
      position: absolute;
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #1888ff;
      transition: 250ms;
    }
  
    
    .mobileshow{
      display:none;
      height: 25px;
    }
    .desktophide{
      display:block;
    }
    .navbar-logo {
      margin-left: 20%;
      
      
    }
    
  }