body{
    padding: 0px;
    margin: 0px;
}
footer{
    margin-left: 0%;

}
.homemargin{
    margin-top: 800px;
}
.person{
    max-height: 550px;
    width: 100%;
}
.homecolor{
    background-color: #fafafa;
   
    
    }
    .h4test{
        color: darkblue;
    }
    .homecard{
        
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        padding: 5px;
    }
    .text-animate{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #fafafa;
    }
    .imgtext{
        position: relative;
         text-align: center;
        color: white;
        font-family: Montserrat, sans-serif;
        
    }
    .typewriter{
        color:white;
        font-size: 35px;
    }
    @media screen and (max-width: 768px) {
        .text-animate{
            position: relative;
            top:200px;
            color: blue;
            
        }  
        .imgtext{
            position: relative;
             text-align: center;
            color: white;
            font-family: Montserrat, sans-serif;
           
        }
        .typewriter{
            margin-bottom: 80px;
            color:blue
        }
    }