:root{
    --primary:#1888ff;
}
.btnhome{
    padding: 8px 10px;
    border-radius: 4px;
    outline: none;
    border: none;
    font-size: 18px;
    color: #fff;
    cursor: pointer;
    background-color: #4C46F6;
}
.btnhome:hover{
    padding: 6px 12px;
    transition: all 0.3s ease-out;
    background-color: transparent;
    color: blue;
    border-radius: 4px;
    border: 2px solid var(--primary);
}