.dropdown-menu2 {
    background:#1888ff;
    width: 200px;
    position: absolute;
    top: 55px;
    list-style: none;
    text-align: start;
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 5px;
    z-index: 1000;
  }
  
  .dropdown-menu2 li {
    background: #1888ff;
    cursor: pointer;
  }
  
  .dropdown-menu2 li:hover {
    background: white
  }
  
  .dropdown-menu2.clicked {
    display: none;
  }
  
  .dropdown-link2 {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 5px;

  }
  .activel{
    background-color: white;
    color: blue;
    font-size: 40;
  }
  
  @media screen and (max-width: 960px) {
    .fa-caret-down {
      
    }
    .dropdown-menu2{
   position: fixed;
   margin-top: 20%;

    }
    .contact{
    clear: both;
       }
    .dropdown-menu2 {
      background:transparent;
      width: 200px;
      position: relative;
      left: 25%;
      list-style: none;
      text-align: first;
      margin-bottom: 2%;
      height: 50%;
    }
    .dropdown-menu2 li {
      background:transparent;
      cursor: pointer;
    }
    .nav-links1{
      text-align: center;
      padding: 2rem;
      width: 100%;
      margin-left: -5%;
